<template>
    <div class="contact-form router-content">
        <h2>Contact/Commision Enquiry</h2>
        <ContactForm v-show="!complete" @submitted="submitted"></ContactForm>
        <ThankYou v-show="complete"></ThankYou>
    </div>
</template>

<script>
export default {
  name: 'contact',
  data: function () {
    return {
      complete: false
    }
  },
  methods: {
    submitted: function (response) {
      this.complete = true
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';
</style>



